    font-size: x-large;
    width: 100%;
    order: 1;
}

.header-sub {
    font-family: 'Syne', sans-serif;
    font-size: x-large;
    color: white;
    text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
